/*
Theme Name: Field 2 starter
*/

@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;600&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	height: 100%;
}

body {
	display: grid;
	min-height: 100%;
	background-color: #000;
	color: #fff;
	font-family: 'Mulish', sans-serif;
	grid-template-rows: auto 1fr auto;
	background-size: 100% auto;
	background-repeat: no-repeat;
	background-position: 0 48px;
	overflow-x: hidden;
}

header {
	position: relative;
	display: flex;
	justify-content: space-between;
	width: 100vw;
	background: #000;

	a {
		color: #fff;
		display: block;
		text-decoration: none;
		padding: 1rem .5rem;
		transition: opacity .3s ease;
		text-transform: lowercase;
		letter-spacing: .2em;
		font-weight: 200;

		&:hover {
			opacity: .7;
		}
	}

	ul {
		list-style: none;
	}
}

main#site_main {
	@media (orientation: landscape) {
		width: 1200px;
		margin: 0 auto;
	}
}

footer#site_footer {
	padding: 1rem;
	font-size: 12px;
}

input#menu-toggle {
	position: absolute;
	top: 0;
	right: 0;
	width: 48px;
	height: 48px;
	display: block;

	@media (orientation: landscape) {
		display: none;
	}

	opacity: 0;
}

a.home_link {
	display: block;
	text-transform: uppercase;
	padding: 1rem;
}

li.current_page_item a {
	font-weight: bold;
}

/* nav */

ul.primary {
	display: flex;
	flex-direction: column;
	background: #000;
	position: absolute;
	width: 100%;
	z-index: 100;
	transform: translateX(100%);
	transition: transform .3s ease;
}

nav.menu-social-container {
	position: relative;

	ul {
		display: flex;
		margin: 0 1rem;
	}
}

div.header_links {
	display: flex;
}

input#menu-toggle {
	&:checked {
		& + ul.primary {
			transform: translateX(-50%);
		}
	}
}

@media (orientation: landscape) {
	input#menu-toggle {
		display: none;
	}

	.hamburger {
		display: none;
	}

	ul.primary {
		position: relative;
		flex-direction: row;
		transform: none;
		margin-right: 2rem;
	}

	div.header_links {
		flex-direction: row-reverse;
	}
}

/* type */
h1, main>h2:first-of-type {
	font-weight: 100;
	margin-top: 3rem;
	color: rgba(255, 255, 255, 0.7);
	text-shadow: 0 0 6px #000;
	font-size: 128px;
}

main  h3 {
	font-weight: 300;
}

// album samples
.samples {
	.wp-block-audio, .wp-block-image {
		margin: 0;
		display: block;
	}

	.wp-block-columns {
		margin-top: .5rem;

		align-items: center;
justify-content: space-between;
.wp-block-column {
	flex-basis:auto;
			opacity:.7;
			transition:opacity .2s ease;
			&:hover {
				opacity:1;
			}
}
		.wp-block-column:first-child {
			width:100%;

		}
	}

	.wp-block-audio audio {
		display: block;
	}
}

// get album buttons 
.wp-block-buttons {
	display: flex;
	justify-content: space-between;
}

// full width video embeds

.wp-block-embed.is-type-video iframe {

    width: 100%;
}